import { Component, h, Host } from '@stencil/core';

@Component({
  tag: 'mt-titulo-h5',
  styleUrl: 'titulo-h5.scss',
  scoped: true,
})
export class TituloH5 {
  render() {
    return (
      <Host class="mt-titulo-h5">
        <h5>
          <slot></slot>
        </h5>
      </Host>
    );
  }
}
